body {
  text-align: center;
  background-color: #5f9ea0;
  margin: 0;
  padding: 30px;
}

h1 {
  color: #fff;
  letter-spacing: 2px;
  font-family: cursive;
  font-size: 60px;
  font-weight: bold;
}

.qr {
  color: red;
}

p {
  letter-spacing: 4px;
  font-size: 35px;
  font-style: italic;
  font-weight: 700;
}

input::placeholder {
  letter-spacing: 1px;
  text-align: center;
  font-style: italic;
  font-weight: bold;
}

input {
  border-style: none;
  border-radius: 6px;
  width: 200px;
  height: 30px;
  padding: 5px;
}

img {
  margin-top: 60px;
  display: block;
}

.qr-code {
  flex-wrap: wrap;
  justify-content: center;
